p,h1,h2,h3,h4,h5,h6{
    margin:0;
    padding: 0;
  }
  
  button {
    cursor: pointer;
    border-radius: 22px;
  }
  
  ul,ol{
    margin: 0;
    padding-left:0;
    list-style: none;
  }

  
  img {
    display: block;
    max-width: 100%;
    
  }

  .list {
    list-style: none;
  }
  
  a {
    text-decoration: none;
  }