.visuallyhidden {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
  }
.btn-up {
    position: fixed;
    bottom: 35vh;
    right: 10vw;
    z-index: 1000;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: none;
    background: #f3b909;
    cursor: pointer;
}

.btn-up:hover {
    background-color: #ffffff;
    transition: all 0.3s;
}


.btn-up-images {
    border: none;
    height:100%;
    width: 100%;
    fill: rgb(0, 0, 0);
}
